'use client'

import { useState, useEffect } from 'react'
import Link from 'next/link'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Progress } from '@/components/ui/progress'

export default function HomePage() {
  const [waterIntake, setWaterIntake] = useState(0)
  const [lastStressLevel, setLastStressLevel] = useState<string | null>(null)
  const [lastSleepHours, setLastSleepHours] = useState<number | null>(null)

  useEffect(() => {
    // Load data from localStorage
    const savedWater = localStorage.getItem('dailyWater')
    const savedStress = localStorage.getItem('lastStressAnalysis')
    const savedSleep = localStorage.getItem('lastSleepHours')
    
    if (savedWater) setWaterIntake(parseInt(savedWater))
    if (savedStress) setLastStressLevel(savedStress)
    if (savedSleep) setLastSleepHours(parseFloat(savedSleep))
  }, [])

  const waterGoal = 8 // 8 glasses per day
  const waterProgress = (waterIntake / waterGoal) * 100

  return (
    <div className="space-y-6 pb-20">
      <div className="text-center space-y-2">
        <h1 className="text-3xl font-bold tracking-tight">Wellness Dashboard</h1>
        <p className="text-muted-foreground">
          Track your stress levels and daily wellness habits
        </p>
      </div>

      <div className="grid gap-4 md:grid-cols-2">
        {/* Stress Level Card */}
        <Card>
          <CardHeader>
            <CardTitle>Stress Level</CardTitle>
            <CardDescription>Latest facial analysis result</CardDescription>
          </CardHeader>
          <CardContent>
            {lastStressLevel ? (
              <div className="space-y-2">
                <p className="text-lg font-medium">{lastStressLevel}</p>
                <Link href="/selfie">
                  <Button variant="outline" size="sm">
                    New Analysis
                  </Button>
                </Link>
              </div>
            ) : (
              <div className="space-y-2">
                <p className="text-muted-foreground">No analysis yet</p>
                <Link href="/selfie">
                  <Button size="sm">
                    Upload Selfie
                  </Button>
                </Link>
              </div>
            )}
          </CardContent>
        </Card>

        {/* Water Intake Card */}
        <Card>
          <CardHeader>
            <CardTitle>Water Intake</CardTitle>
            <CardDescription>Daily hydration progress</CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-3">
              <div className="flex justify-between text-sm">
                <span>{waterIntake} glasses</span>
                <span>{waterGoal} goal</span>
              </div>
              <Progress value={waterProgress} className="h-2" />
              <Link href="/water">
                <Button variant="outline" size="sm">
                  Log Water
                </Button>
              </Link>
            </div>
          </CardContent>
        </Card>

        {/* Sleep Card */}
        <Card>
          <CardHeader>
            <CardTitle>Sleep Tracking</CardTitle>
            <CardDescription>Last night's rest</CardDescription>
          </CardHeader>
          <CardContent>
            {lastSleepHours ? (
              <div className="space-y-2">
                <p className="text-lg font-medium">{lastSleepHours} hours</p>
                <Link href="/sleep">
                  <Button variant="outline" size="sm">
                    Update Sleep
                  </Button>
                </Link>
              </div>
            ) : (
              <div className="space-y-2">
                <p className="text-muted-foreground">No sleep data</p>
                <Link href="/sleep">
                  <Button size="sm">
                    Track Sleep
                  </Button>
                </Link>
              </div>
            )}
          </CardContent>
        </Card>

        {/* Quick Actions Card */}
        <Card>
          <CardHeader>
            <CardTitle>Quick Actions</CardTitle>
            <CardDescription>Wellness tools and tips</CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-2">
              <Link href="/typing">
                <Button variant="outline" size="sm" className="w-full">
                  Check Typing Speed
                </Button>
              </Link>
              <Link href="/tips">
                <Button variant="outline" size="sm" className="w-full">
                  Stress Relief Tips
                </Button>
              </Link>
            </div>
          </CardContent>
        </Card>
      </div>

      <Card>
        <CardHeader>
          <CardTitle>Today's Wellness Summary</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-2 gap-4 text-center">
            <div>
              <p className="text-2xl font-bold text-primary">{waterIntake}</p>
              <p className="text-sm text-muted-foreground">Glasses of water</p>
            </div>
            <div>
              <p className="text-2xl font-bold text-primary">
                {lastSleepHours ? `${lastSleepHours}h` : '--'}
              </p>
              <p className="text-sm text-muted-foreground">Hours of sleep</p>
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
